import MDBox from "../../components/MDBox"
import { forwardRef, useState } from "react"
import { Card, Slide, TextField } from "@mui/material"
import MDButton from "../../components/MDButton"
import MDTypography from "../../components/MDTypography"
import { Link } from "react-router-dom"
import QRCode from "react-qr-code"
import { supabase } from "../../supabaseClient"
import { useAuth } from "../../components/context"
import { useToast } from "@dalgu/react-toast"
import assuredWorkLoadSimpleIcon from "../../icons/assuredWorkLoadSimpleIcon"
import maybeIcon from "../../icons/maybeIcon"
import { useNavigate } from "react-router-dom"
import GoBackBtn from "../../components/GoBackBtn/GoBackBtn"

const blockchains = [
  {
    value: "BSC",
    label: "BSC"
  },
  {
    value: "ERC20",
    label: "ERC20"
  }
]
const Transition = forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />
})

function AddWalletPage() {
  const [blockchain, setBlockchain] = useState("")
  const [address, setAddress] = useState("")
  const { showToast } = useToast()
  const navigate = useNavigate()

  const handleChange = event => {
    if (event.target.name === "blockchain") {
      setBlockchain(event.target.value)
    } else if (event.target.name === "address") {
      setAddress(event.target.value)
    }
  }

  const [open, setOpen] = useState(false)
  const user = useAuth()

  const addWallet = async () => {
    const { data, error } = await supabase.from("wallets").insert({
      address: address,
      wallet_name: address,
      owned_by: user.user_metadata.username,
      blockchain: blockchain
    })
    if (error) {
      showToast("You need to put at least 20 characters to add a new wallet!")
    } else {
      showToast("Wallet added successfully")
      navigate("/balance")
    }
  }

  const handleClose = () => {
    setOpen(false)
  }
  return (
    <MDBox sx={{ display: "flex", flexDirection: "column", height: "100%", justifyContent: "space-between" }}>
      <MDBox sx={{ paddingBottom: "88px" }}>
        <br />
        <br />
        <Card>
          <MDBox display="flex" alignItems="center" pt={3} px={1.5}>
            <GoBackBtn boxShadow="none" onWhite="#344767" />
            <MDTypography variant="h4" sx={{ display: "flex", alignItems: "center", gap: "5px" }}>
              {assuredWorkLoadSimpleIcon}
              Add New Wallet
            </MDTypography>
          </MDBox>
          <MDBox px={3} pb={3}>
            <TextField
              sx={{ mt: 3, width: "100%", minWidth: "80px" }}
              id="outlined-basic"
              label="Wallet Address"
              variant="outlined"
              onChange={handleChange}
              value={address}
              name="address"
              helperText="Please insert the address of your wallet"
            />
            <TextField
              SelectProps={{
                native: true
              }}
              select
              sx={{ mt: 2, width: "100%", minWidth: "80px" }}
              id="outlined-basic"
              label="Blockchain"
              variant="outlined"
              value={blockchain}
              onChange={handleChange}
              name="blockchain"
              helperText="Please select one blockchain">
              <option aria-label="None" value="" />
              {blockchains.map(option => (
                <option key={option.value} value={option.value}>
                  {option.label}
                </option>
              ))}
            </TextField>
            {address && (
              <>
                <MDBox alignItems="center" display="flex" justifyContent="center" mt={2}>
                  <MDTypography fontSize={12} color="dark">
                    {" "}
                    In order to prevent issues, please verify your wallet by sending <b>1 USDT</b> to the below address. <br />
                  </MDTypography>
                  {maybeIcon}
                </MDBox>
                <MDBox display="flex" justifyContent="center" mt={2}>
                  <QRCode value={"he24820342049fye0o03032" + address} />
                </MDBox>
                <MDBox display="flex" justifyContent="center" mt={2}>
                  <MDTypography fontSize={14} color="text">
                    he24820342049fye0o03032
                  </MDTypography>
                </MDBox>
                <MDBox display="flex" justifyContent="center" mt={2}>
                  <MDTypography fontSize={10} color="dark">
                    If you have any question please contact your sponsor or the support.
                  </MDTypography>
                </MDBox>
              </>
            )}
            <MDBox mt={3} display="flex" justifyContent="end">
              <MDButton component={Link} to={"/balance"} sx={{ mr: 1, width: "100%" }}>
                Back
              </MDButton>
              <MDButton disabled={!address} sx={{ width: "100%" }} color="info" variant="gradient" onClick={addWallet}>
                Next
              </MDButton>
            </MDBox>
          </MDBox>
        </Card>
      </MDBox>
    </MDBox>
  )
}

export default AddWalletPage
